<template>
  <div class="wrap">
    <!-- 播放器主体区域 -->
    <div class="play_wrap" id="player">
      <div class="search_bar">
        <img src="./images/player_title.png" alt="" />
        <!-- 搜索歌曲 -->
        <input
          type="text"
          autocomplete="off"
          v-model="query"
          @keyup.enter="searchMusic"
        />
      </div>
      <div class="center_con">
        <!-- 搜索歌曲列表 -->
        <div class="song_wrapper">
          <ul class="song_list">
            <li v-for="(item, index) in musicList" :key="index">
              <a href="javascript:;" @click="playMusic(item.id)"></a>
              <b>{{ item.name }}</b>
              <span v-if="item.mvid != 0" @click="playMV(item.mvid)"
                ><i></i
              ></span>
            </li>
          </ul>
          <img src="./images/line.png" class="switch_btn" alt="" />
        </div>
        <!-- 歌曲信息容器 -->
        <div class="player_con" :class="{ playing: isPlaying }">
          <img src="./images/player_bar.png" class="play_bar" />
          <!-- 黑胶碟片 -->
          <img src="./images/disc.png" class="disc autoRotate" />
          <img :src="musicCover" class="cover autoRotate" />
        </div>
        <!-- 评论容器 -->
        <div class="comment_wrapper">
          <h5 class="title">热门留言</h5>
          <div class="comment_list">
            <dl v-for="(item, index) in hotComments" :key="index">
              <dt><img :src="item.user.avatarUrl" alt="" /></dt>
              <dd class="name">{{ item.nickname }}</dd>
              <dd class="detail">
                {{ item.content }}
              </dd>
            </dl>
          </div>
          <img src="./images/line.png" class="right_line" />
        </div>
      </div>
      <div class="audio_con">
        <audio
          ref="audio"
          @play="play"
          @pause="pause"
          :src="musicUrl"
          controls
          autoplay
          loop
          class="myaudio"
        ></audio>
      </div>
      <div class="video_con" v-show="isShow" style="display: none">
        <video :src="mvUrl" controls="controls" autoplay></video>
        <div class="mask" @click="hide"></div>
      </div>
    </div>
  </div>
</template>
<!-- 这样引用的css样式才能作为局部样式使用 -->
<style scoped src="./index.css"></style>
<script>
import axios from "../../../store/axios";
export default {
  created() {
    this.baseUrl = axios.defaults.baseURL;
    axios.defaults.baseURL = "https://autumnfish.cn/";
  },
  destroyed() {
    axios.defaults.baseURL = this.baseUrl;
  },
  data() {
    return {
      baseUrl: "",
      query: "",
      musicList: [],
      isPlaying: false,
      musicCover: "",
      hotComments: [],
      musicUrl: "",
      isShow: false,
      mvUrl: "",
    };
  },
  methods: {
    searchMusic() {
      // 请求接口之前 对所需要的参数进行必要的探空处理
      if (!this.query.trim()) {
        return;
      }
      axios.get("search?keywords=" + this.query).then((data) => {
        console.log(data);
        if (data.code == 200) {
          data = data.result;
          this.musicList = data.songs;
        } else {
          this.$alert("请输入歌曲关键词", "错误提示", {
            confirmButtonText: "确定",
          });
        }
      });
    },
    playMusic(id) {
      if (id) {
        axios.get("song/url?id=" + id).then((data) => {
          if (data.code == 200) {
            if (data.data && data.data[0]) {
              data = data.data[0];
              if (data.url) {
                this.musicUrl = data.url;
              }
            }
          }
        });

        axios.get("song/detail?ids=" + id).then((data) => {
          if (data.code == 200) {
            if (
              data.songs &&
              data.songs[0] &&
              data.songs[0].al &&
              data.songs[0].al.picUrl
            ) {
              let url = data.songs[0].al.picUrl;
              this.musicCover = url;
            }
          }
        });

        axios.get("comment/hot?type=0&id=" + id).then((data) => {
          if (data.code == 200) {
            if (data.hotComments && data.hotComments[0]) {
              this.hotComments = data.hotComments;
            }

            if (data.topComments && data.topComments[0]) {
              this.hotComments.unshift(data.topComments);
            }
          }
        });
      }
    },
    playMV(id) {
      axios.get("mv/url?id=" + id).then((data) => {
        if (data.code == 200) {
          console.log(data);
          if (data.data && data.data.url) {
            this.mvUrl = data.data.url;
            this.isShow = true;
          }
        }
      });
    },
    play() {
      this.isPlaying = true;
    },
    pause() {
      this.isPlaying = false;
    },
    hide() {
      this.isShow = false;
      this.mvUrl = "";
    },
  },
};
</script>